.club_cards_section {
  width: 100%;
  position: relative;
  margin-top: rem(150px);

  .club_card_title {
    opacity: 0.12;
    color: #ffcc01;
    font-family: "Proxima Nova";
    font-size: rem(126px);
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
  }

  .card_title_item {
    font-family: "Proxima Nova";
    font-size: rem(60px);
    font-weight: 700;
    text-align: center;
    color: #ffcc01;
    text-transform: uppercase;
    position: relative;
    top: rem(-70px);
  }
  .container {
    position: relative;
    .club_wrap {
      width: 100%;
      margin-top: rem(85px);
      display: flex;
      position: relative;
      .club_card {
        display: flex;
        flex-direction: column;
        width: 100%;
        max-width: rem(260px);
        margin-left: rem(30px);
        &:first-child {
          margin-left: 0;
        }
        .card_text {
          color: #1f1f1f;
          font-family: Lato;
          font-size: rem(17px);
          font-weight: 400;
        }
        .card_price {
          color: #1f1f1f;
          font-family: "Proxima Nova";
          font-size: rem(17px);
          font-weight: 700;
        }
      }
      .club_btn {
        position: absolute;
        top: rem(63px);
        right: rem(-145px);
        transform: rotate(90deg);
        padding: rem(20px) rem(40px);
        border: 3px solid #ffcc01;
        color: #1f1f1f;
        font-family: "Proxima Nova";
        font-size: rem(16px);
        font-weight: 700;
        text-transform: uppercase;
        transition: .4s;
        &:hover {
          background-color: #ffcc01;
          color: #ffffff;
        }
      }
    }
  }
}

/*CARDS SECTION*/
.cards_section {
  width: 100%;
  position: relative;
  overflow: hidden;
  &::before {
    @include before_fig(100%, 100%, rgba(6, 6, 6, 0.85));
    top: 0;
    left: 0;
    z-index: 1;
  }
  .parallax {
    position: absolute;
  }
  .club_card_title {
    position: absolute;
    z-index: 2;
  }
  .card_title {
    position: relative;
    z-index: 2;
    left: rem(-150px);
    text-align: center;
    margin-top: 226px;
    color: #ffcc01;
    font-family: "Proxima Nova";
    font-size: 60px;
    font-weight: 700;
    text-transform: uppercase;

    span {
      font-size: rem(126px);
      opacity: 0.05;
      position: absolute;
      top: rem(-64px);
      left: rem(216px);
    }
  }
  .container {
    margin-top: rem(120px);
    max-width: rem(1263px);
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    padding-bottom: rem(50px);
    z-index: 2;
    .club_wrap {
      background-color: #fff;
      padding: rem(80px) rem(60px) 0;
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      .club_card {
        margin-bottom: rem(50px);
        flex: 1 1 rem(263px);

      }
    }
    .club_control {
      width: 100%;
      height: 100px;
      background-color: transparent;
      display: flex;
      justify-content: flex-end;
      position: relative;
      align-items: center;
      padding-right: rem(65px);
      &::before {
        @include before_fig(100%, 100%, rgba(255, 255, 255, 0.1));
        top: 0;
        left: 0;
        z-index: -1;
      }
      .icon-left-open, .icon-right-open {
        font-size: rem(25px);
        color: #ffcc01;

      }
      .control_elem {
        position: relative;
        color: #fffcf1;
        font-size: rem(17px);
        font-weight: 700;
        margin-left: rem(25px);
        display: block;
        &.active {
          &::before {
            @include before_fig(rem(17px), rem(3px), #ffcc01);
            bottom: -24%;
            left: -41%;
          }
            &:first-child{
              margin-left:0;
            }

        }

      }

    }
    .club_footer {
      margin-top: rem(60px);
      position: relative;
      display: flex;
      justify-content: space-between;
      padding: 0 rem(80px);
      .left_nav {
        a {
          color: #989898;
          font-size: rem(14px);
          font-weight: 700;
          margin-left: rem(40px);
          text-transform: uppercase;
          transition: .4s;
          position: relative;
          &:hover {
            color: #ffcc01;
          }
          &:first-child {
            margin-left: 0;
            &::before {
              @include before_fig(rem(4px), rem(4px), #ffcc01);
              right: -37%;
              top: 45%;
            }
          }
        }
      }
    }
  }
}


